"use client";

import { useState, useCallback } from "react";
import { useRouter } from "next/navigation";
import { Video, Users, Copy, ArrowRight, Loader2 } from "lucide-react";
import { motion, AnimatePresence } from "framer-motion";
import { Button } from "@/components/ui/button";
import { Input } from "@/components/ui/input";
import { Card, CardHeader, CardTitle, CardDescription, CardContent } from "@/components/ui/card";
import { useToast } from "@/hooks/use-toast";

export default function Meet() {
  const router = useRouter();
  const { toast } = useToast();
  const [roomId, setRoomId] = useState("");
  const [inputRoomId, setInputRoomId] = useState("");
  const [isLoading, setIsLoading] = useState(false);

  const createMeeting = async () => {
    try {
      setIsLoading(true);
      const newRoomId = Math.random().toString(36).substring(7);

      const response = await fetch("/api/meetings", {
        method: "POST",
        headers: {
          "Content-Type": "application/json",
        },
        body: JSON.stringify({ roomId: newRoomId }),
      });

      if (!response.ok) throw new Error("Failed to create meeting");

      const meeting = await response.json();
      setRoomId(meeting.roomId || newRoomId);
      toast({
        title: "Meeting Created",
        description: "Your meeting room is ready!",
      });
    } catch (error) {
      console.error("Error creating meeting:", error);
      toast({
        variant: "destructive",
        title: "Error",
        description: "Failed to create meeting. Please try again.",
      });
    } finally {
      setIsLoading(false);
    }
  };

  const joinMeeting = useCallback((id: string) => {
    if (!id.trim()) {
      toast({
        variant: "destructive",
        title: "Error",
        description: "Please enter a valid room ID",
      });
      return;
    }
    router.push(`/meeting/${id}`);
  }, [router, toast]);

  const copyRoomId = async () => {
    try {
      await navigator.clipboard.writeText(roomId);
      toast({
        title: "Copied!",
        description: "Room ID copied to clipboard",
      });
    } catch (error) {
      toast({
        variant: "destructive",
        title: "Error",
        description: "Failed to copy room ID",
      });
    }
  };

  return (
    <div className="min-h-[calc(100vh-4rem)] bg-gradient-to-b from-background to-muted/20 flex items-center justify-center p-6">
      <motion.div 
        initial={{ opacity: 0, y: 20 }}
        animate={{ opacity: 1, y: 0 }}
        className="w-full max-w-4xl space-y-8"
      >
        <div className="text-center space-y-4">
          <h1 className="text-4xl font-bold tracking-tight">Video Meetings</h1>
          <p className="text-muted-foreground max-w-2xl mx-auto">
            Connect with anyone, anywhere. Start or join a high-quality video meeting with just a click.
          </p>
        </div>

        <div className="grid md:grid-cols-2 gap-6">
          <Card>
            <CardHeader>
              <CardTitle className="flex items-center gap-2">
                <Video className="w-5 h-5 text-primary" />
                Start a Meeting
              </CardTitle>
              <CardDescription>
                Create a new meeting and invite others to join
              </CardDescription>
            </CardHeader>
            <CardContent className="space-y-4">
              <Button
                onClick={createMeeting}
                className="w-full"
                disabled={isLoading}
              >
                {isLoading ? (
                  <>
                    <Loader2 className="mr-2 h-4 w-4 animate-spin" />
                    Creating...
                  </>
                ) : (
                  <>
                    Create Meeting
                    <ArrowRight className="ml-2 h-4 w-4" />
                  </>
                )}
              </Button>

              <AnimatePresence>
                {roomId && (
                  <motion.div
                    initial={{ opacity: 0, height: 0 }}
                    animate={{ opacity: 1, height: "auto" }}
                    exit={{ opacity: 0, height: 0 }}
                    className="space-y-4"
                  >
                    <div className="flex items-center gap-2 p-3 bg-muted rounded-lg">
                      <code className="flex-1 text-sm">{roomId}</code>
                      <Button
                        variant="secondary"
                        size="icon"
                        onClick={copyRoomId}
                      >
                        <Copy className="h-4 w-4" />
                      </Button>
                    </div>
                    <Button
                      variant="default"
                      className="w-full"
                      onClick={() => joinMeeting(roomId)}
                    >
                      Join Now
                    </Button>
                  </motion.div>
                )}
              </AnimatePresence>
            </CardContent>
          </Card>

          <Card>
            <CardHeader>
              <CardTitle className="flex items-center gap-2">
                <Users className="w-5 h-5 text-primary" />
                Join a Meeting
              </CardTitle>
              <CardDescription>
                Enter a room ID to join an existing meeting
              </CardDescription>
            </CardHeader>
            <CardContent className="space-y-4">
              <Input
                type="text"
                value={inputRoomId}
                onChange={(e) => setInputRoomId(e.target.value)}
                placeholder="Enter Room ID"
                className="w-full"
              />
              <Button
                variant="default"
                className="w-full"
                onClick={() => joinMeeting(inputRoomId)}
                disabled={!inputRoomId.trim()}
              >
                Join Meeting
                <ArrowRight className="ml-2 h-4 w-4" />
              </Button>
            </CardContent>
          </Card>
        </div>
      </motion.div>
    </div>
  );
}